.text-image-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include mw(740) {
    flex-direction: column-reverse; }

  &_high {
    height: 750px; }

  &_reverse {
    flex-direction: row-reverse;

    @include mw(740) {
      flex-direction: column-reverse; }

    .text-image-block__image {
      &_cover {
        width: 100%;
        max-width: 1085px;
        position: absolute;
        left: 0;
        top: 0; } } }

  &__text {
    width: 100%;
    max-width: 675px;
    color: $text-grey;
    @include mw(1600) {
      max-width: 40%; }
    @include mw(740) {
      max-width: 100%; }

    &_narrow {
      max-width: 520px; } }

  &__title {
    font-size: 40px;
    line-height: 1.3;
    margin-bottom: 20px;
    @include mw(1024) {
      font-size: 28px; }
    @include mw(740) {
      display: none; } }

  &__title_mobile {
    display: none;
    @include mw(740) {
      display: block;
      font-size: 28px;
      line-height: 1.3;
      margin-bottom: 40px; } }

  &__subtitle {
    font-size: 28px;
    line-height: 1.3; }

  &__content {
    margin-bottom: 70px;
    @include mw(1024) {
      font-size: 14px;
      margin-bottom: 40px; }
    @include mw(740) {
      margin-bottom: 20px; } }

  &__buttons {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  &__image {
    width: 790px;
    height: 537px;
    align-self: baseline;
    @include mw(1600) {
      width: 50%; }
    @include mw(740) {
      width: 100%;
      height: 290px; }
    @include mw(420) {
      height: 190px; }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover; }

    &_cover {
      width: 100%;
      max-width: 1085px;
      position: absolute;
      right: 0;
      top: 0; } } }




