.contact-card {
  display: flex;
  flex-wrap: wrap;

  &__title {
    font-size: 36px;
    line-height: 1.4;
    font-weight: 800;
    color: $text-grey;
    margin-bottom: 46px;
    @include mw(1024) {
      font-size: 24px;
      margin-bottom: 25px; } }

  &__subtitle {
    font-size: 20px;
    line-height: 1.3;
    font-weight: 600;
    color: $purple;
    margin-bottom: 12px;
    @include mw(1024) {
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 10px; } }

  &__address {
    width: 100%;
    margin-bottom: 40px;
    @include mw(1024) {
      margin-bottom: 30px; } }

  &__address-name {
    font-size: 26px;
    line-height: 1.3;
    margin-bottom: 10px;
    @include mw(1024) {
      font-size: 16px; } }

  &__address-description {
    font-size: 20px;
    line-height: 1.3;
    @include mw(1024) {
      font-size: 14px; } }

  &__phones {
    width: 50%;
    @include mw(425) {
      width: 100%;
      margin-bottom: 20px; } }

  &__phone {
    margin-bottom: 10px;

    a {
      font-size: 18px;
      color: $text-grey;
      @include mw(1024) {
        font-size: 18px; } } }

  &__work-schedule {
    width: 50%;
    @include mw(425) {
      width: 100%; }

    li {
      font-size: 18px;
      margin-bottom: 10px;
      @include mw(1024) {
        font-size: 16px; } } } }

