html {
  font-family: Mont, sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: $text-grey; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  //overflow: hidden
  @include mw(1600) {
    padding: 0 20px; } }

img {
  &._contain {
    object-fit: contain !important; } }

p {
  &._bold {
    font-weight: 600 !important; } }

.list-flex {
  display: flex;
  flex-wrap: wrap;

  &_sb {
    justify-content: space-between; } }
.no-hide {
  overflow: unset;
  @include mw(1920) {
    overflow: hidden; } }

.section {
  padding: 70px 0;
  margin-bottom: 100px;
  @include mw(1200) {
    padding: 50px 0;
    margin-bottom: 70px; }
  @include mw(1024) {
    padding: 40px 0;
    margin-bottom: 60px; }
  @include mw(740) {
    padding: 20px 0;
    margin-bottom: 40px; }

  &_no-padding {
    padding: 0; }

  &_no-margin {
    margin-bottom: 0; }

  &_grey {
    background-color: $light-grey; }

  &_cover-image {
    position: relative;
    margin-bottom: 0;
    overflow: hidden; }

  &__title {
    text-align: center;
    margin-bottom: 70px;
    @include mw(1200) {
      margin-bottom: 50px; }
    @include mw(1024) {
      margin-bottom: 30px; }
    @include mw(640) {
      margin-bottom: 16px; } } }



.relative {
  position: relative; }
