.header {
  &-desc {
    @include mw(1100) {
      display: none; } }
  &_top {
    background-color: $light-grey;
    padding: 8px 0;

    .container {
      display: flex;
      position: relative; } }

  &_bottom {
    padding: 14px 0;
    border-bottom: 1px solid #E4E4E4;

    .container {
      display: flex;
      justify-content: space-between; } }

  &-callback {
    display: flex;
    align-items: center;
    margin-left: auto;

    &__phone {
      color: $icons-grey;
      margin-right: 48px; } }

  &__logo {
    display: block; }
  &-mobile {
    display: none;
    padding: 20px 0;
    @include mw(1100) {
      display: block; }
    &__container {
      display: flex;
      flex-wrap: wrap; }
    &__logo {
      width: 135px;
      height: 21px;
      margin-bottom: 12px; }
    &__phone {
      width: calc(100% - 135px);
      text-align: right;
      font-size: 16px;
      line-height: 20px;
      color: $text-grey;
      span {
        color: $purple; }
      &:hover {
        span {
          color: $text-grey; } } }

    &__search {
      display: block;
      width: calc(100% - 36px);
      position: relative;
      @include b_image('icons/search-mobile.svg', '26px', 'right');
      &:before {
        content: 'Поиск';
        font-size: 17.5225px;
        line-height: 22px;
        color: $green;
        position: absolute;
        @include centered('y');
        right: 50px; } }

    &__bookmarks {
      width: calc(100% - 36px);
      display: none;
      justify-content: flex-end;
      .header-bookmarks {
        @include b_image('icons/bookmark-white.svg'); } }
    &__hr {
      display: none;
      width: calc(100% + 20px);
      height: 1px;
      background-color: $text-grey;
      margin: 20px 0; }
    &__get-callback {
      width: 100%;
      display: none; }
    &-nav {
      &__list {
        width: 100%;
        height: 0;
        overflow: hidden; }
      &__item {
        width: 100%;
        margin-bottom: 20px;
        span, a {
          font-size: 16px;
          line-height: 20px;
          color: $yellow; }
        &_drop-down {
          span {
            position: relative;
            &:after {
              content: '';
              display: block;
              width: 20px;
              height: 7px;
              @include b_image('icons/drop-down-yellow-top-arrow.svg');
              position: absolute;
              top: calc(50% - 3.5px);
              right: -40px;
              transform: rotate(180deg);
              transition: all .5s; } } }
        &_drop-down.show-drop-down-menu {
          span {
            &:after {
              transform: rotate(0deg); } } } }
      &__drop-down-container {
        height: 0;
        overflow: hidden; }
      &__dropdown-menu {
        padding: 30px 0 0 20px; }
      &__dropdown-item {
        margin-bottom: 15px;
        a {
          font-size: 14px;
          line-height: 18px;
          color: #EFEFEF; } } }

    &_open-mobile-menu {
      background-color: $icons-grey;
      .logo_rect {
        @include b_image('icons/logo-header-mobile-white.svg'); }
      .header-mobile {
        border: 1px solid $text-grey;
        &__phone {
          color: $white;
          span {
            color: $yellow; } }
        &__search {
          display: none; }
        &__bookmarks {
          display: flex; }
        &__get-callback {
          display: flex; }
        &__hr {
          display: block; }
        &-nav {
          &__list {
            background-color: $icons-grey; } } } } }


  &-search-block {
    display: flex;
    align-items: center; }

  &-bookmarks {
    display: block;
    width: 30px;
    height: 30px;
    @include b_image('icons/bookmark.svg');
    position: relative;
    margin-right: 20px;

    &__count {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 18px;
      height: 18px;
      border-radius: 100%;
      background-color: $yellow;
      font-size: 12px;
      position: absolute;
      top: -8px;
      right: -5px; } }

  &-search-form {
    width: 250px;
    height: 40px;
    position: relative;

    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      background-color: $light-grey;
      padding: 11px 16px; }

    &:after {
      display: block;
      content: '';
      width: 22px;
      height: 22px;
      @include b_image('icons/search.svg');
      position: absolute;
      right: 16px;
      @include centered('y'); } } }

.dropdown-li {
  &:hover {
    position: relative !important;
    z-index: 2;

    &:after {
      display: none; }

    &:before {
      content: '';
      width: calc(100% + 32px);
      height: 69px;
      background-color: $light-grey;
      @include centered();
      z-index: -1; }

    .header-nav__dropdown-menu {
      transform: scale(1); } } }


.header-nav {
  &_top {
    width: 100%;
    max-width: 370px;
    display: flex;
    align-items: center;
    @include centered();

    &__list {
      width: 100%;
      display: flex;
      justify-content: space-between; }

    &__item.active {
      a {
        color: $purple; } }

    &__item {
      a {
        color: $text-grey;
        text-decoration: none;
        transition: all .3s;

        &:hover {
          color: $purple; } } } }

  &_bottom {
    width: 100%;
    max-width: 706px;
    display: flex;
    align-items: center;
    @include mw(1320) {
      max-width: 500px; }

    &__list {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center; }

    &__item.active {
      position: relative;

      &:after {
        display: block;
        content: '';
        width: 100%;
        height: 4px;
        background-color: $purple-focused;
        position: absolute;
        left: 0;
        bottom: -20px;
        transform: scale(1);
        z-index: -2; } }

    &__item {
      position: relative;
      transition: all .3s;

      &:after {
        display: block;
        content: '';
        width: 100%;
        height: 4px;
        background-color: $purple-focused;
        position: absolute;
        left: 0;
        bottom: -20px;
        transform: scale(0);
        transition: all .3s; }

      &:hover {
        &:after {
          transform: scale(1); } }

      a {
        color: $text-grey;
        text-decoration: none; } } }

  &__dropdown-menu {
    display: block;
    background-color: $light-grey;
    position: absolute;
    left: -16px;
    top: calc(50% + 35px);
    transform: scale(0);
    transition: all .3s;

    li {
      width: 280px;
      height: 52px;
      display: flex;
      align-items: center;
      padding: 0 23px 0 16px;

      &:hover {
        background-color: $purple;

        a {
          color: $white; } } } } }



