.image-border {
  position: relative;
  z-index: 2;

  &:before {
    content: '';
    width: 100%;
    height: 100%;
    border: 2.5px solid $black;
    @include image-border-position('right');
    position: absolute;
    z-index: -1; }

  &_light-blue {
    &:before {
      border: 2.5px solid $light-blue; } }

  &_green {
    &:before {
      border: 2.5px solid $green; } }

  &_pink {
    &:before {
      border: 2.5px solid $pink; } }

  &_yellow {
    &:before {
      border: 2.5px solid $yellow; } }

  &_purple {
    &:before {
      border: 2.5px solid $purple; } }

  &_left {
    &:before {
      @include image-border-position('left'); } } }
